import { Component, h, Host } from '@stencil/core';

/**
 * This component is only used in our documentations
 * to provide a container for enriching the guidelines with
 * good and bad examples.
 *
 * @private
 */
@Component({
    tag: 'limel-example-do-do-not',
    shadow: true,
    styleUrl: 'example-do-do-not.scss',
})
export class ExampleDoDoNot {
    public render() {
        return (
            <Host>
                <section class="do">
                    <div>
                        <slot name="do" />
                        <footer>
                            <limel-icon name="checkmark" aria-hidden="true" />
                            <h1>Do</h1>
                        </footer>
                    </div>
                </section>
                <section class="do-not">
                    <div>
                        <slot name="do-not" />
                        <footer>
                            <limel-icon name="multiply" aria-hidden="true" />
                            <h1>Don't</h1>
                        </footer>
                    </div>
                </section>
            </Host>
        );
    }
}
